.hamburger{
    height: 2rem;
}

/* For desktop devices */
@media screen and (min-width: 900px){
    .hamburger{
        display: none;
    }    
}

.hamburger:hover{
    cursor: pointer;
}

.hamburger div{
    position: relative;
    width: 2rem;
    height: 2px;
    transition: 0.2s ease-in-out;
}

.hamburger-light div{
    background-color: black;
}

.hamburger-dark div{
    background-color: white;
}

.hamburger .div1{
    top: 0;
}

.hamburger .div2{
    top: 0.7rem;
}

.hamburger .div3{
    top: 1.4rem;
}

.active .div1 {
    opacity: 0;
}

.active .div2 {
    transform: rotateZ(-45deg);
}

.active .div3 {
    transform: rotateZ(45deg) translate(-0.65rem, -0.65rem); 
}