.outer-container{
    min-height: 30rem;    
    padding: 10rem;
}

.contact-light{
    color: black;    
    background-color: white;
    background-image: url("./light.svg");
}

.contact-dark{
    background-color: black;
    background-image: url("./dark.svg");
    color: white;
}

.header{
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
}

.header h1{
    font-size: 4rem;
}

.header h3{
    margin-top: 2rem;
    font-size: 1.5rem;
    font-weight: 400;
    line-height: 3rem;
}

.contact-form-container{
   padding: 5rem; 
   margin: 0 15rem;
   border-radius: 1rem;
   display: flex;
   flex-direction: column;
   gap: 2rem;
}

@media (max-width: 1500px){
    .contact-form-container{
        margin: 0;
    }
}

.contact-form-container div{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.contact-form-container div label{
    font-size: 1.4rem;
}

.contact-form-container div input{
    border: none;
    height: 5rem;
    font-size: 1.5rem;
    padding: 0 2rem;
    border-radius: 0.5rem;
}

.contact-light .contact-form-container div input,
.contact-light .contact-form-container div textarea{
    background-color: #eee;
    color: black;
}

.contact-dark .contact-form-container div input,
.contact-dark .contact-form-container div textarea{
    background-color: #111;
    color: white;
}

.contact-form-container div input:hover,
.contact-form-container div input:focus{
    outline: none;
}

.contact-form-container div textarea{
    border: none;
    height: 25rem;
    min-height: 20rem;
    font-size: 1.5rem;
    padding: 2rem;
    border-radius: 0.5rem;
    min-width: 100%;
    max-width: 100%;
}

.contact-form-container div textarea:hover,
.contact-form-container div textarea:focus{
    outline: none;
}

.contact-light .contact-form-container{
    background-color: #fff;
    box-shadow: 0px 0px 2rem #eee;
}

.contact-dark .contact-form-container{
    background-color: #222;
    box-shadow: 0px 0px 2rem #444;
}

.submit-button-container div{
    margin-left: auto;
}