.outer-container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 5rem;
}

.about-light{
    color: black;
}

.about-dark{
    color: white;
}

.about-me{
    grid-column-start: 1;
    grid-column-end: 5;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
}

.about-me h1{
    font-size: 4rem;
}

.about-me h3{
    margin-top: 2rem;
    font-size: 1.5rem;
    font-weight: 400;
}


/* Media queries */
@media (min-width: 900px){
    .know-me{
        grid-column-start: 1;
        grid-column-end: 3;
    }
    .my-skills{
        grid-column-start: 3;
        grid-column-end: 5;
    }
    .skillset{
        width: 50rem;
        height: 50rem;
        margin-left: auto;    
        margin-bottom: -50%;
    }
    .my-skills span{
        transform-origin: 0 25rem;
        font-size: 3rem;
    }
}

@media (max-width: 900px){
    .know-me{
        grid-column-start: 1;
        grid-column-end: 5;
        padding: 0 16%;
    }
    .my-skills{
        padding: 0 16%;
        grid-column-start: 1;
        grid-column-end: 5;
        padding: 0 16%;
    }
    .skillset{
        margin-left: 0;
        width: 20rem;  
        height: 20rem;
        margin-bottom: -20%;
    }
    .my-skills span{
        transform-origin: 0 10rem;
        left: 20%;
        font-size: 1.5rem;
    }
}

@media (max-width: 600px){
    .know-me{
        padding: 0 2%;
    }
    .my-skills{
        padding: 0 2%;
        grid-column-start: 1;
        grid-column-end: 5;
    }
    .skillset{
        display: flex;
        margin-left: 0;
        width: 15rem;  
        height: 15rem;
        margin-bottom: -10%;
        animation: none;
    }
    .my-skills span{
        transform-origin: 0 7.5rem;
        font-size: 1rem;
    }
}

.know-me h2{
    font-size: 3rem;
}

.know-me p{
    font-size: 1.5rem;
}

.my-skills h2{
    width: fit-content;
   font-size: 3rem; 
}

/* My skills  */
.skillset{
    position: relative;
    animation: rotateText 15s linear infinite;
}

@keyframes rotateText{
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}

.my-skills span{
    position: absolute;
    left: 50%;
}