.nav{
    display: flex;
    height: 10rem;
    padding: 0 8rem;
    align-items: center;
    width: 100%;
    transition: 0.2s ease-in-out; 
}

/* For desktop devices */
@media screen and (min-width: 900px){
    .nav-secton{
        align-items: flex-end;
    }
    .nav-section .nav-section-links-hamburger{
        display: none;
    }
    .expanded-nav{
        display: none;
    }
}

/* For tablet devices */
@media screen and (max-width: 1000px){
    .profile h2{
        display: none;
    }
}

/* For mobile devices */
@media screen and (max-width : 900px){
    .nav{
        padding: 0 2rem;
    }
    .nav-section .nav-section-links{
        display: none;
    }
    .expanded-nav{
        display: flex;
    }
}

.nav-light{
    color: black;
    background-color: #f6f6f6; 
    box-shadow: 0px 2px 5px #ccc;
}

.nav-dark{
    color: white;
    background-color: #222; 
    box-shadow: 0px 2px 5px #333;
}

.profile{
    height: 60%;
    display: flex;
    align-items: center;
    gap: 3rem;
}

.profile img{
    height: 100%;
    border-radius: 100%;
}

.nav-light .profile img{
    border: 2px solid #333;
}

.nav-dark .profile img{
    border: 2px solid #ccc;
}

.profile h2{
    font-size: 2rem;
}

.nav-section{
    margin-left: auto;
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    gap: 4rem;
}

.nav-section-links{
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    gap: 4rem;
}

.nav-light a{
    color: black;
}

.nav-dark a{
    color: white;
}

.nav-outer a{
    font-weight: 500;
    text-decoration: none;
}

.resume-link{
   background-color: gray;
   padding: 1rem 2rem; 
   border-radius: 0.5rem;
}

.resume-link:hover{
    background-color: #aaa;
}


/* Styles for expanded nav */
.expanded-nav-outer{
    transition: 0.3s ease-in-out;
    background-color: inherit;
}

.expanded-nav{
    justify-content: space-evenly;
    padding: 2rem;
    align-items: center;
    font-size: 1.5rem;
    position: absolute;
    width: 100%;
    background-color: inherit;
}

.nav-dark .expanded-nav{
    border-top: 1px solid #333;
}

.nav-light .expanded-nav{
    border-top: 1px solid #eee;
}
