.sidebar{
    display: flex;
    position: absolute;
    font-size: 3rem;
    padding: 2rem 3rem;
    align-items: center;
    transition: 0.2s ease-in-out;
}

/* For desktop devices */
@media screen and (min-width: 900px){
    .sidebar{
        left: 0;
        flex-direction: column;
        width: 5rem;
        border-radius: 0.5rem;
    }
    .sidebar:hover{
        transform: scale(1.3) translateX(0.6rem); 
    }
}

/* For mobile devices */
@media screen and (max-width : 900px){
    .sidebar{
        bottom: 0;
        right: -1rem;
        height: 5rem;
        border-top-left-radius: 0.5rem;
        border-bottom-left-radius: 0.5rem;
    }
}

.sidebar-light{
    background-color: white;
    box-shadow: 0px 0px 1.5rem #aaa;
}

.sidebar-dark{
    background-color: #222;
    box-shadow: 0px 0px 1.5rem #555;
}

.sidebar-light a{
    color: black;
}

.sidebar-dark a{
    color: white;
}

.social-icon{
    font-size: 0.5rem;
}

.sidebar a{
    padding: 1rem 1rem 0.5rem;
    border-radius: 1rem;
    transition: 0.3s ease-in-out;
}

.sidebar-light a:hover{
    background-color: rgb(208, 239, 252);
}

.sidebar-dark a:hover{
    background-color: red;
}